<template id="">
  <div class="test">
    <input ref="phoneNum" type="number" @input="phoneOnInput" placeholder="请输入手机号" v-model="phone">
    <div class="text">
      <span>input的值：{{this.phone}}</span>
    </div>
  </div>
</template>

<script type="text/javascript">
  export default {
    data() {
      return {
        phone: ''
      }
    },
    methods: {
      phoneOnInput(e) {
        console.log(e.target.value)
      }
    }
  }
</script>
<style>
  .test {
    margin: 20px 40px;
  }
  .text {
    margin: 20px 40px;
  }
</style>



